<template>
  <div id="PianoMemory" class="pb-16">
    
    <!-- <Alert ref="Alert"/> -->

    
    <!-- <InteractiveBord @dataForApp="updateTimeOver" @changeSetting="changeSettings()" :score="score" :maxW="'max-w-xl'" ref="InteractiveBord"/> -->


    <div class="w-full flex flex-col items-center font-bold">
      <div class="w-full flex flex-col max-w-xl mt-3 px-5 text-2xl">
        
        <div class="container w-full flex rounded-lg overflow-hidden">
          <span class="w-full pt-3 bg-red-800"></span>
          <span :style="'width:'+timeRunFlashList" class="absolute pt-3 bg-green-600"></span>
        </div>

        <div class="w-full flex flex-wrap">
          <div class="container w-1/2 mt-6" :class="item.isFlash ? 'flash':''" v-for="(item,index) in items" :key="index" @click="isclick(index)">
            <div class="mainView bg-red-400 mx-3 px-8 py-10 rounded-lg items-center text-center">
              {{ index }}
            </div>
            <div class="overlay bg-purple-900 mx-20 my-5 px-8 py-10 rounded-full flex flex-col items-center" />
          </div>
        </div>
        
      </div>
    </div>
    
    <!-- <LevelsSelect @dataForApp="updateLevelValue" /> -->
    
  </div>
</template>

<script>
//import Alert from '../components/Alert'
//import InteractiveBord from '../components/InteractiveBord'
//import LevelsSelect from '../components/LevelsSelect'
export default {
  name: 'PianoMemory',
  components: {
  //  Alert,
  //  InteractiveBord,
  //  LevelsSelect
  },
  data() {
    return {
      flashList:[],
      userClick:0,

      stopGame:false,

      timeRunFlashList:"100%",

      items:[
        {
          id:0,
          isFlash:false
        },
        {
          id:1,
          isFlash:false
        },
        {
          id:2,
          isFlash:false
        },
        {
          id:3,
          isFlash:false
        }
      ]
    }
  },
  created() {
    this.MStart();
  },
  methods: {
    MStart(){
      setTimeout(() => {
        this.addNumberToFlashList();
      }, 1000);
    },
    isclick(id){
      if(this.stopGame)return;
      this.addFlashClass(id,300)
      if(id == this.flashList[this.userClick]){
        console.log("yes")
      }else {
        this.stopGame = true;
        console.log("you Loss")
        return
      }

      this.userClick++;

      if(this.userClick == this.flashList.length){
        this.userClick = 0;
        setTimeout(() => {
          this.addNumberToFlashList();
        }, 1000);
        console.log("this Level complet")
      }
    },
    addNumberToFlashList(){
      var num = Math.floor(Math.random() * this.items.length)
      this.flashList.push(num)

      this.runFlashList();
    },
    runFlashList(id = 0){
      if (id > this.flashList.length) {
        console.log("done")
        return
      }
      this.addFlashClass(this.flashList[id],1000)
    
      setTimeout(() => {
        this.runFlashList(id + 1)
      }, 1500);
    },
    addFlashClass(id,time){
      clearTimeout(this.flashTimeOut)
      var item = this.items[id];
      if(!item)return;

      this.items.forEach(item => {
        item.isFlash = false
      });

      item.isFlash = true
      this.flashTimeOut = setTimeout(() => {
        item.isFlash = false
      }, time);
    },
    updateTimeRunFlashList(id){
      var time = Math.floor((id / this.flashList.length)*100)
      time = 100 - time
      this.timeRunFlashList = time+"%"
    }
  },
  computed: {
    
  },
}
</script>

